@import '../../index.scss';

.Rule {
  display: flex;
  flex-direction: row;
  border-bottom: solid 1px #ededed;
}

.RuleID {
  width: 30%;
  height: 82px;
  opacity: 0.5;
  margin-left: 13px;
  font-family: Roboto;
  font-size: 12px;
  margin-top: 10px;
  margin-right: 12px;
  /* padding: 9px; */
  /* font-weight: normal; */
  /* font-stretch: normal; */
  /* font-style: normal; */
  /* line-height: 1.17; */
  /* letter-spacing: normal; */
  text-align: left;
  color: #000000;
}
.RuleDisplay {
  .hover-container {
    display: flex;
    flex-direction: row;
    padding: 10px 0px 10px 5px;
    margin-left: 5px;
    font-size: 12px;
    border-right: solid 1px #ededed;
    border-bottom: solid 1px #ededed;
    cursor: pointer;
    color: #666666;

    &:hover {
      background-color: $PrimaryActiveBg;
    }
  }
}
.RuleIDDisplay {
  width: 20%;
  font-family: Roboto;
  font-size: 14px;
  padding: 4px;
  text-align: left;
  color: #000000;
  text-transform: uppercase;
}
.RuleNameDisplay {
  width: 20%;
  margin-left: 20px;
  font-family: Roboto;
  font-size: 14px;
  padding: 3px;
  text-align: left;
  color: #000000;
}

.RuleID-inputfields {
  width: 100%;
  height: 38px;
  //   opacity: 0.4;
  border-radius: 5px;
  margin-top: 5px;
  border: solid 1px #d3d3d3;
  background-color: #ffffff;
}
.RuleName {
  width: 70%;
  height: 82px;
  opacity: 0.5;
  /* margin-left: 13px; */
  font-family: Roboto;
  font-size: 12px;
  margin-top: 10px;
  /* margin-right: 71px; */
