@import '../../../../index.scss';

.business-event-page {
  .BusinessEventInstances {
    height: 100%;
  }
  .BusinessEvents {
    width: 33%;
    height: 100%;
  }
  .Business-EventListHeaderBox {
    display: flex;
    flex-direction: row;
    height: 30px;
    line-height: 2.3;
    backdrop-filter: blur(30px);
    border: solid 1px #ededed;
    background-color: #ffffff;
    cursor: pointer;
  }
  .BusinessEvent_Box {
    width: 100%;
    height: 58px;
    background-color: #f8f8f8;
    border: solid 1px #e7e8f7;

    border-top-left-radius: 10px;

    display: flex;
    flex-direction: row;
    padding: 13px 7px 0px 7px;

    margin-top: 0px;
  }

  .Business-EventList-details {
    display: flex;
    width: 100%;
    flex-direction: row;
    height: 75px;
    background-color: #ffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    padding-bottom: 0%;
    letter-spacing: normal;
    border-bottom: solid 1px #ededed;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &:hover {
      background-color: $PrimaryActiveBg;
    }
  }
  .BusinessListName-Area {
    width: 17%;
  }

  .BusinessListID {
    height: 14px;
    font-family: Roboto;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    padding-left: 14px;
  }
  .BusinessListNameandBaseBody {
    width: 55%;
    white-space: normal;
  }
  .BusinessListName {
    //height: 26px;
    font-family: Roboto;
    font-size: 12px;
    text-align: left;
    color: #000000;
    padding-left: 14px;
  }
  .BusinessListBaseBody {
    font-family: Roboto;
    font-size: 12px;
    font-weight: normal;
    margin-top: 5px;
    width: 145%;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    color: #666666;
    padding-top: 5px;
  }
  .Business-Event-Transactional-button-Container {
    width: 27%;
    // position: absolute;
    // right: 5px;
    top: 15px;
    text-transform: capitalize;
  }
  .Business-Event-Transactional-button-Area {
    width: 100%;
    text-transform: capitalize;
    .Business-Event-Transactional-button {
      height: 16px;
      font-family: Roboto;
      border: solid 1px #e2e2e2;
      font-size: 8px;

      font-stretch: normal;
      font-style: normal;

      letter-spacing: 0.35px;
      color: #909090;
      background-color: #f8f8f8;
      text-transform: uppercase;
    }
  }
  .Business-searchContainer {
    width: 100%;

    background-color: #f8f8f8;
    padding: 1.5px 4px 16px 0px;
  }
  .Business-Event-search-header {
    position: relative;
    background-color: #f8f8f8;
    .Business-Event-search-icon {
      position: absolute;
      width: 16px;
      height: 26px;
      line-height: 26px;
      color: #999999;
      left: 7px;
    }

    .Business-EventListSearchBar {
      width: 100%;
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      height: 26px;
      font-family: Roboto;
      font-size: 12px;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #999999;
      padding-left: 28px;
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      height: 26px;
      font-family: Roboto;
      font-size: 12px;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #999999;
      padding-left: 28px;
    }
  }
  .Business-EventListHeaderAddFonts {
    flex: 1;
    height: 14px;
    font-family: Roboto;
    font-size: 12px;
    color: #4951c3;
    text-align: left;
    padding-left: 10px;
  }

  .Business-EventListAddIcon {
    flex: 0.1;
    font-family: Roboto;
    color: #4951c3;
    cursor: pointer;
  }
  .Business-EventListBox {
    display: flex;
    flex-direction: column;
    height: calc(100% - 78px);
    background-color: #ffff;
    border: solid 1px #e7e8f7;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    overflow: scroll;

    .Business-EventListDetails {
      display: flex;
      height: auto;
      position: relative;
      background-color: #ffff;
      font-family: Roboto;
      font-size: 14px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.14;
      padding: 10px;
      letter-spacing: normal;
      border-bottom: solid 1px #ededed;
      border-right: 2px solid transparent;
      cursor: pointer;
      white-space: nowrap;
      text-overflow: ellipsis;
      &.activeInstance {
        background-color: $PrimaryActiveBg;
        border-right: 2px solid #4a51c3;
      }
      &:hover {
        background-color: $PrimaryActiveBg;
      }
    }

    .EventListHeader {
      width: 100%;
      height: 25px;
      background-color: #ffff;
      font-family: Roboto;
      font-size: 14px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.14;
      letter-spacing: normal;

      text-align: center;
      color: #666666;
      margin-top: 10px;
      border-bottom: 1px solid #ededed;
    }
    .EventListHeaderdraft {
      width: 100%;
      height: 25px;
      background-color: #ffff;
      font-family: Roboto;
      font-weight: 500;
      font-size: 14px;
      text-align: left;
      color: #666666;
      margin-top: 6px;
      border-bottom: 1px solid #ededed;
    }
  }
  .BusinessEvent-left-box {
    height: 100%;
  }
  .EventListDetails {
    display: flex;
    flex-direction: column;
    height: auto;
    background-color: #ffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    padding-bottom: 0;
    letter-spacing: normal;
    border-bottom: solid 1px #ededed;
    cursor: pointer;
    white-space: nowrap;
    text-overflow: ellipsis;
    &.activeInstance {
      background-color: $PrimaryActiveBg;
      border-right: 2px solid #4a51c3;
    }
    &:hover {
      background-color: $PrimaryActiveBg;
    }

    .Business-Event-Transactional-button-Area {
      width: 29%;
      padding-right: 5px;

      margin-top: 10px;
      .Business-Event-Transactional-button {
        width: 100%;
        height: 16px;
        font-family: Roboto;
        border: solid 1px #e2e2e2;
        font-size: 8px;

        font-stretch: normal;
        font-style: normal;

        letter-spacing: 0.35px;
        color: #909090;
        background-color: #f8f8f8;
        text-transform: uppercase;
      }
    }
  }

  .BusinessEventInstances {
    width: 100%;
    display: flex;
    flex-direction: row;
    // width: 256px;
    height: 100%;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: center;
    color: #c3c3c3;
    backdrop-filter: blur(30px);
    background-color: #f8f8f8;

    .Selection-panel {
      width: 67%;
      border-top-right-radius: 4px;
      background-color: #fff;
      height: 100%;
      overflow-y: scroll;
    }
    .Selection-image {
      display: flex;
      height: 90vh;
      justify-content: center;
      align-items: center;
    }
  }

  .no-event {
    display: flex;
    height: 90vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
