.business-event-page {
  .businessevent-criteria {
    .addEvent {
      width: 100%;
      color: #666666;
      text-align: left;
      font-weight: bold;
      font-size: 14px;
      background-color: #ffffff;

      .addEventText {
        display: flex;
        color: #666666;
        padding: 13px 25px 12.5px 8px;
        background-color: #ffffff;
      }

      .bottomline {
        width: 100%;
        border: solid 1px #ededed;
      }

      .eventDetails {
        margin: 10.5px 10px 0px 8px;

        .Name {
          font-size: 12px;
          opacity: 0.5;
        }

        .eventinput {
          padding-top: 7px;
        }

        .description {
          height: 83px;
          font-size: 14px;
          font-weight: bold;
          font-family: roboto;
          resize: none;
          outline: none;
          width: 100%;
          border: solid 1px #d3d3d3;
          border-radius: 5px;
          padding: 10px;

          &::placeholder {
            font-weight: 500;
          }
        }
      }
    }

    .criteria-list {
      padding-top: 15px;
      margin-bottom: 60px;
    }
  }
}
