@import '../../index.scss';

.Schedulling-attributestext {
  background-color: #f8f8f8;
  text-align: left;
  color: #666666;
  font-size: 14px;
  padding: 10px;
}

.Schedulling-attributes {
  font-size: 14px;
  text-align: left;
  font-weight: normal;
  padding: 10px 10px 0px 10.5px;
  min-height: 40vh;

  .Schedulling-heading {
    font-family: Roboto;
    font-size: 12px;
    padding-top: 12px;
    color: #000000;
    opacity: 0.5;
  }

  .Schedulling-Snooze-time-heading {
    font-family: Roboto;
    font-size: 12px;
    padding-top: 12px;
    color: #000000;
    opacity: 0.5;
  }

  .Schedulling-Snooze-time-inputfields {
    padding-top: 7px;
    position: relative;
    .minute {
      position: absolute;
      right: 15px;
      top: 56%;
      transform: translateY(-50%);
    }
  }

  .Schedulling-Snooze-box {
    width: 23%;
    // margin-right: 5px;
    /* padding-right: 5px; */
    padding-top: 5px;
    padding-right: 5px;
  }
  .Schedulling-Snooze-time-box {
    width: 23%;
    padding: 5px;
    // margin-top: 5px;
    // padding-left: 6px;
  }

  .Schedulling-inputfields {
    padding-top: 7px;
  }

  .Schedulling-Snooze-and-IncrementalValue-box {
    display: flex;
    flex-direction: row;
  }
  .Schedulling-description {
    height: 83px;
    font-size: 14px;
    font-weight: bold;
    font-family: roboto;
    resize: none;
    outline: none;
    width: 100%;
    border: solid 1px #d3d3d3;
    border-radius: 5px;
    padding: 10px;

    &::placeholder {
      font-weight: 500;
    }
  }

  .Schedulling-SaveCancelButton {
    display: flex;
    float: right;
    padding: 18px 0px 11px 0px;

    .Schedulling-save {
      background-color: #4b50c3;
      color: #ffffff;
    }
  }
}
