@import '../../../../index.scss';
.business-event-page {
  .signal-container {
    height: 80vh;
    // overflow-y: scroll;

    display: flex;
    .signals {
      flex: 0.5;
      border-right: 1px solid #ededed;
      .criteria-header {
        padding-left: 5px;
        .header {
          text-align: left;
          padding: 10px 4px;
          font-size: 15px;
          font-weight: bold;
          background: #f8f8f8;
          margin-left: -5px;
        }
      }
      .add-signal {
        position: relative;
        display: flex;
        color: $PrimaryTextColor;
        padding: 8px 0;
        border-bottom: solid 0.5px #ededed;
        .add-signal-button {
          border: transparent;
          color: $PrimaryTextColor;
          outline: none;
          margin-left: 0px;
          &:hover {
            background: transparent;
            color: inherit;
          }
        }
      }
      .add-signal-container {
        text-align: left;
        .signal-name {
          font-size: 13px;
          opacity: 0.5;
          color: #000;
        }
        .signal-name,
        .signal-input {
          margin: 7px 0;
        }
        .signal-controller {
          display: flex;
          justify-content: flex-end;
          button.add-signal-button {
            background-color: $PrimaryBgColor;
            color: #fff;
          }
        }
      }
      .criteria-list-container {
        .criteria-body {
          font-size: 13px;
          border-right: 2px solid transparent;

          .criteria-list {
            margin: 0;
            padding: 15px;
            border-bottom: solid 0.5px #ededed;
            text-align: left;
            color: #000;
          }
          &.active-signal {
            background-color: #ebedfd;
            border-right: 2px solid #4a51c3;
          }
        }
      }
    }
    .attribute-container {
      flex: 0.5;

      .criteria-header {
        padding-left: 5px;
        .header {
          text-align: left;
          padding: 10px 4px;
          font-size: 15px;
          font-weight: bold;
          background: #f8f8f8;
          margin-left: -5px;
        }
      }
      .add-attribute-button-container {
        position: relative;
        display: flex;
        color: $PrimaryTextColor;
        padding: 8px 0;
        border-bottom: solid 0.5px #ededed;
        .add-attribute-button {
          width: auto;
          border: transparent;
          color: $PrimaryTextColor;
          outline: none;
          margin-left: 0px;
          &:hover {
            background: transparent;
            color: inherit;
          }
        }
      }
      .add-attribute-container {
        padding-left: 5px;
        .attribute-name {
          font-size: 13px;
          opacity: 0.5;
          color: #000;
          text-align: left;
        }
        .attribute-name,
        .attribute-input {
          margin: 7px 0;
        }
        .attribute-controller {
          display: flex;
          justify-content: flex-end;
          button.add-attribute-button {
            background-color: $PrimaryBgColor;
            color: #fff;
          }
        }
      }
      .attribute-list-container {
        .criteria-body {
          font-size: 13px;
          border-right: 2px solid transparent;
          .criteria-list {
            margin: 0;
            padding: 15px;
            border-bottom: solid 0.5px #ededed;
            text-align: left;
          }
          &.active-signal {
            background-color: #ebedfd;
            border-right: 2px solid #4a51c3;
          }
        }
      }
    }
    .attribute-list-container {
      overflow: scroll;
      height: 70vh;
    }
  }
}
