@import '../../../index.scss';

// header
$EventManagementMain: 65%;
$EventManagementManage: 35%;
$headerHeight: 26px;
$headerStartPosition: 21px;

.business-event-page {
  .EventManagementHeader {
    margin: 10px 0;
    margin-top: $headerStartPosition;
    width: 100%;
    height: $headerHeight;
    overflow: hidden;
  }
  .Events {
    width: 33%;
    height: 60px;
    overflow: hidden;
  }
  .config-container {
    height: calc(100% - 60px);
  }

  .EventManagementwrap {
    display: flex;
  }
  .EventManagementText {
    color: #666666;
    opacity: 0.7;
    font-size: 14px;
    flex: 1;
    height: 26px;
    line-height: 26px;
    padding-left: 10px;
  }
  .Event-header-right-content {
    flex: 1.1;
    display: flex;
    justify-content: flex-end;
  }

  .event-search-header {
    position: relative;
    .event-search-icon {
      position: absolute;
      width: 18px;
      height: $headerHeight;
      line-height: $headerHeight;
      color: #999999;
      left: 7px;
    }
    .EventManagementSearchBar {
      width: 100%;
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      height: 26px;
      font-family: Roboto;
      font-size: 12px;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #999999;
      padding-left: 28px;
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      height: $headerHeight;
      font-family: Roboto;
      font-size: 12px;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #999999;
      padding-left: 28px;
    }
  }
  .event-header-create {
    position: relative;
    width: 65%;
    .event-create-icon {
      position: absolute;
      width: 11px;
      height: 26px;
      line-height: 26px;
      font-weight: bold;
      color: #fff;
      left: 11px;
      font-size: 12px;
    }
    .CreateEventTemplate {
      width: 100%;
      border-radius: 20px;
      margin-left: 0px;
      color: #ffffff;
      background-color: #4b50c3;
      height: 26px;
      font-size: 12px;
      font-family: 'Roboto';
      font-stretch: normal;
      font-style: normal;
      line-height: 23px;
      letter-spacing: normal;
      text-align: left;
      padding-left: 23px;
      outline: none;
      white-space: nowrap;
      overflow: hidden;
    }
    .Searchbartesxtbox {
      width: 108px;
      border-radius: 20px;
      margin-left: 5px;
      color: #ffffff;
      background-color: #4b50c3;
      height: 26px;
      font-size: 12px;
      font-family: 'Roboto';
      font-stretch: normal;
      font-style: normal;
      line-height: 26px-3;
      letter-spacing: normal;
      text-align: left;
      padding-left: 28px;
    }
  }
}
